import React, { useState, useEffect } from "react";
import { Form, Input, Select, Button, Radio, InputNumber } from "antd";
import { handleForm } from "@/redux/actions/form";
import { connect } from "react-redux";
import { restoreFormData } from "../../../utils/productUtil";
const { Option } = Select;
const AddStepOne = (props) => {
  const [levels, setLevels] = useState(
    props.formData ? props.formData.memberLevel : 0
  );
  const [onSale, setOnSale] = useState("yes");
  const [formData, setFormData] = useState(
    props.formData ? props.formData : null
  );
  const [productType, setProductType] = useState(
    props.formData ? props.formData.productType : 1
  );
  let formRef = React.createRef();
  useEffect(() => {
    let url = document.location.toString();
    let idArr = url.split("/");
    let id = idArr[idArr.length - 1];
    if (!isNaN(parseInt(id))) {
      //productUitl
      formRef.current.setFieldsValue(
        restoreFormData(props.allProducts[id - 1])
      );
      setFormData(restoreFormData(props.allProducts[id - 1]));
      setLevels(props.allProducts[id - 1].memberLevel);
      setProductType(props.allProducts[id - 1].productType);
    }
  }, []);
  const onLevelChange = (value) => {
    setLevels(parseInt(value));
  };

  const onSaleChange = (e) => {
    setOnSale(e.target.value);
  };
  const onTypeChange = (e) => {
    setProductType(e.target.value);
  };

  const onFinish = (values) => {
    props.handleFormData(values);
    props.handleForm(values);
    props.handleNext();
    setFormData(values);
  };

  const renderLevelDesc = () => {
    let arr = [];
    for (let i = 1; i <= levels; i++) {
      arr.push(i);
    }
    return arr.map((item) => {
      return (
        <div key={item}>
          <Form.Item
            name={`levelName${item}`}
            label={`${item}`}
            rules={[
              {
                required: true,
                message: `${item}`,
              },
            ]}
          >
            <Input placeholder={""} />
          </Form.Item>
          <Form.Item label={`${item}`}>
            <Input.Group compact>
              <Form.Item name={[`levelPrice${item}`, `price${item}`]} noStyle>
                <InputNumber
                  min={0}
                  step={1}
                  style={{
                    width: "calc(100% - 100px)",
                  }}
                  placeholder={`${item}`}
                />
              </Form.Item>
              <Form.Item
                name={[`levelPrice${item}`, `unit${item}`]}
                noStyle
                rules={[
                  { required: true, message: `${item}` },
                ]}
              >
                <Select
                  style={{
                    width: 100,
                  }}
                  placeholder=""
                >
                  <Option value=""></Option>
                  <Option value="3">3</Option>
                  <Option value="6">6</Option>
                  <Option value=""></Option>
                  <Option value=""></Option>
                </Select>
              </Form.Item>
            </Input.Group>
          </Form.Item>
          <Form.Item
            name={`levelDesc${item}`}
            label={`${item}`}
            rules={[
              {
                required: true,
                message: "",
              },
            ]}
          >
            <Input.TextArea
              id="desc"
              placeholder=""
              autoSize={{ minRows: 3, maxRows: 5 }}
            />
          </Form.Item>
          <Form.Item label={`${item}`} name={`levelLimit${item}`}>
            <InputNumber
              style={{ width: "100%" }}
              min={0}
              step={1}
              placeholder=""
            />
          </Form.Item>
          <Form.Item name={`levelNote${item}`} label={`${item}`}>
            <Input.TextArea
              id="note"
              placeholder=""
              autoSize={{ minRows: 3, maxRows: 5 }}
            />
          </Form.Item>
        </div>
      );
    });
  };

  const formItemLayoutWithOutLabel = {
    wrapperCol: {
      xs: { span: 24, offset: 12 },
      sm: { span: 20, offset: 11 },
    },
  };
  const formItemLayout = {
    labelCol: {
      sm: { span: 8 },
    },
    wrapperCol: {
      sm: { span: 8 },
    },
  };
  return (
    <div className="shadow-radius" style={{ marginTop: "50px" }}>
      <Form
        {...formItemLayout}
        onFinish={onFinish}
        initialValues={formData ? formData : null}
        ref={formRef}
      >
        <Form.Item
          label=""
          name="productType"
          rules={[
            {
              required: true,
              message: "",
            },
          ]}
        >
          <Radio.Group onChange={onTypeChange} value={productType}>
            <Radio value={1}></Radio>
            <Radio value={2}></Radio>
          </Radio.Group>
        </Form.Item>
        <a
          href="https://github.com/troyeguo/coodo-pay#"
          target="_blank"
          rel="noopener noreferrer"
        >
          <div
            style={{ textAlign: "center", marginBottom: 20, marginTop: -20 }}
          >
            
          </div>
        </a>
        <Form.Item
          label=""
          name="productName"
          rules={[
            {
              required: true,
              message: "",
            },
          ]}
        >
          <Input placeholder="" />
        </Form.Item>
        <Form.Item
          name="productInfo"
          label=""
          rules={[
            {
              required: true,
              message: "",
            },
          ]}
        >
          <Input.TextArea
            placeholder="20"
            autoSize={{ minRows: 3, maxRows: 5 }}
          />
        </Form.Item>

        <Form.Item
          label=""
          name="memberLevel"
          rules={[
            {
              required: true,
              message: "",
            },
          ]}
        >
          <Select placeholder="" onChange={onLevelChange}>
            <Option value="1">1</Option>
            <Option value="2">2</Option>
            <Option value="3">3</Option>
            <Option value="4">4</Option>
          </Select>
        </Form.Item>
        {renderLevelDesc()}
        {productType === 1 ? null : (
          <Form.Item
            label=""
            name="callbackUrl"
            rules={[
              {
                type: "url",
                required: productType === 1 ? false : true,
                message: "",
              },
            ]}
          >
            <Input placeholder="" />
          </Form.Item>
        )}

        <Form.Item
          label=""
          name="onSale"
          rules={[
            {
              required: true,
              message: "",
            },
          ]}
        >
          <Radio.Group onChange={onSaleChange} value={onSale}>
            <Radio value="yes"></Radio>
            <Radio value="no"></Radio>
          </Radio.Group>
        </Form.Item>
        <Form.Item
          name={`contact`}
          label={``}
          rules={[
            {
              required: true,
              message: "",
            },
          ]}
        >
          <Input.TextArea
            id="contact"
            placeholder=""
            autoSize={{ minRows: 3, maxRows: 5 }}
          />
        </Form.Item>
        <Form.Item {...formItemLayoutWithOutLabel}>
          <Button type="primary" htmlType="submit">
            
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    formData: state.form.formData,
    allProducts: state.product.allProducts,
  };
};
const actionCreator = {
  handleForm,
};
export default connect(mapStateToProps, actionCreator)(AddStepOne);
