import React, { useState } from "react";
import {
  Steps,
  Button,
  message,
  Row,
  Form,
  Input,
  Checkbox,
  Result,
} from "antd";
import { Link } from "react-router-dom";
import $axios from "@/axios/$axios";
const { Step } = Steps;
const formItemLayoutWithOutLabel1 = {
  wrapperCol: {
    xs: { span: 24, offset: 12 },
    sm: { span: 16, offset: 11 },
  },
};
const formItemLayoutWithOutLabel2 = {
  wrapperCol: {
    xs: { span: 24, offset: 12 },
    sm: { span: 16, offset: 5 },
  },
};
const formItemLayout1 = {
  labelCol: {
    sm: { span: 6, offset: 0 },
  },
  wrapperCol: {
    sm: { span: 16, offset: 0 },
  },
};
const formItemLayout2 = {
  labelCol: {
    sm: { span: 0, offset: 1 },
  },
  wrapperCol: {
    sm: { span: 20, offset: 2 },
  },
};
const CollectInfo = (props) => {
  const [current, setCurrent] = useState(0);
  const [formData, setFormData] = useState(null);

  const onFinish = (values) => {
    if (values.password === values.passwordAgain) {
      setFormData({ ...formData, ...values });
      if (current === 1) {
        $axios
          .post("/user", formData)
          .then(() => {
            next();
          })
          .catch(() => {
            message.error("");
          });
      } else {
        next();
      }
    } else {
      message.warning("");
    }
  };
  const next = () => {
    setCurrent(current + 1);
  };
  const prev = () => {
    setCurrent(current - 1);
  };
  const steps = [
    {
      title: "",
      content: (
        <Form
          onFinish={onFinish}
          initialValues={formData ? formData : null}
          style={{ marginTop: "40px" }}
          defaultValue={formData}
          {...formItemLayout1}
        >
          <Form.Item
            label=""
            name="email"
            rules={[
              {
                type: "email",
                message: "The input is not valid E-mail!",
              },
              {
                required: true,
                message: "",
              },
            ]}
          >
            <Input placeholder="" />
          </Form.Item>
          <Form.Item
            label=""
            name="password"
            rules={[
              {
                required: true,
                message: "",
              },
            ]}
          >
            <Input.Password placeholder="" />
          </Form.Item>
          <Form.Item
            label=""
            name="passwordAgain"
            rules={[
              {
                required: true,
                message: "",
              },
            ]}
          >
            <Input.Password placeholder="" />
          </Form.Item>

          <Form.Item {...formItemLayoutWithOutLabel1}>
            <Button type="primary" htmlType="submit">
              
            </Button>
          </Form.Item>
        </Form>
      ),
    },
    {
      title: "",
      content: (
        <Form
          onFinish={onFinish}
          initialValues={formData ? formData : null}
          style={{ marginTop: "40px" }}
          {...formItemLayout2}
        >
          <Form.Item
            name="safeCheck"
            label=""
            rules={[
              {
                required: true,
                message: "",
              },
            ]}
          >
            <Checkbox.Group>
              <Checkbox value="" style={{ lineHeight: "32px" }}>
                
              </Checkbox>
            </Checkbox.Group>
          </Form.Item>
          <Form.Item
            label=""
            name="answer1"
            rules={[
              {
                required: true,
                message: "",
              },
            ]}
          >
            <Input placeholder="" />
          </Form.Item>
          <Form.Item
            label=""
            name="answer2"
            rules={[
              {
                required: true,
                message: "",
              },
            ]}
          >
            <Input placeholder="" />
          </Form.Item>

          <Form.Item {...formItemLayoutWithOutLabel2}>
            <Button type="primary" htmlType="submit" className="collect-finish">
              
            </Button>
          </Form.Item>
          <Button
            onClick={() => {
              prev();
            }}
            className="collect-prev"
          >
            
          </Button>
        </Form>
      ),
    },
    {
      title: "",
      content: (
        <Result
          status="success"
          title=""
          subTitle=""
          extra={
            <div>
              <Link to="/">
                <Button type="primary" size="large">
                  
                </Button>
              </Link>
            </div>
          }
          className="result"
          style={{ marginTop: "20px" }}
        ></Result>
      ),
    },
  ];
  return (
    <div>
      <img src="/assets/tech.svg" alt="" className="welcome-bg" />
      <Row justify="center" className="collect-steps-container">
        <Steps current={current} className="collect-steps">
          {steps.map((item) => (
            <Step key={item.title} title={item.title} />
          ))}
        </Steps>
        <div className="steps-content" className="collect-steps-box">
          {steps[current].content}
        </div>
      </Row>
    </div>
  );
};

export default CollectInfo;
