import React, { useState } from "react";
import { Row, Button, Modal } from "antd";
const WelcomePage = (props) => {
  const [visible, setVisible] = useState(false);
  const showModal = () => {
    setVisible(true);
  };
  const handleOk = () => {
    props.handleCurrent("collectInfo");
  };

  const handleCancel = () => {
    setVisible(false);
  };
  return (
    <div>
      <img src="/assets/tech.svg" alt="" className="welcome-bg" />
      <Row justify="center" className="welcome-title">
         Coodo Pay
      </Row>
      <Row justify="center" className="welcome-subtitle">
         React  Koa2 
      </Row>
      <Row justify="center" className="welcome-info">
         Coodo Pay
        <br />
        
      </Row>
      <Row justify="center" className="welcome-button">
        <Button type="primary" onClick={showModal} size="large">
          
        </Button>
      </Row>

      <Modal
        visible={visible}
        title=""
        onOk={handleOk}
        onCancel={handleCancel}
        footer={[
          <Button key="back" onClick={handleCancel}>
            
          </Button>,
          <Button key="submit" type="primary" onClick={handleOk}>
            
          </Button>,
        ]}
      >
        <p>
          Coodo Pay
          
        </p>
      </Modal>
    </div>
  );
};

export default WelcomePage;
