import React, { useEffect } from "react";
import "./index.css";
const Captcha = (props) => {
  useEffect(() => {
    window
      .vaptcha({
        vid: "5eac198c33cb1801abf079ab", // id
        type: "click", //  
        scene: 0, //  0
        container: "#vaptchaContainer", // Element  selector
        offline_server: "", //
        //
        // lang: 'zh-CN', //  zh-CN,zh-CN,en,zh-TW,jp
        // https: true, // https  true
        //style: 'dark' // dark dark,light
        //color: '#57ABFF' // #57ABFF
      })
      .then((vaptchaObj) => {
        vaptchaObj.render(); //  vpObj  render 
        //token
        //vaptchaObj.renderTokenInput('.login-form')//formtoken
        //token
        vaptchaObj.listen("pass", () => {
          // 
          props.handleQuery();
        });
        //
        vaptchaObj.listen("close", function () {
          //
        });
      });
  });
  return (
    <div
      id="vaptchaContainer"
      style={{ width: 200, height: 36, marginLeft: 150 }}
    >
      <div className="vaptcha-init-main">
        <div className="vaptcha-init-loading">
          <a href="/" target="_blank">
            <img
              src="https://r.vaptcha.net/public/img/vaptcha-loading.gif"
              alt="loading"
            />
          </a>
          <span className="vaptcha-text">Vaptcha...</span>
        </div>
      </div>
    </div>
  );
};

export default Captcha;
