import React, { useState, useEffect } from "react";
import { Table, Button, Input, DatePicker, Badge, message } from "antd";
import moment from "moment";
import { connect } from "react-redux";
import $axios from "@/axios/$axios";
import { handleFetchOrder } from "@/redux/actions/form";
const dateFormat = "YYYY-MM-DD";
const { Search } = Input;

const OrderPage = (props) => {
  const [data, setData] = useState(props.order);
  const [loading, setLoading] = useState(true);
  const [refundLoading, setRefundLoading] = useState(false);
  const [refundIndex, setRefundIndex] = useState(-1);
  const [selectedRowKeys, setSelectedRowKeys] = useState([]);
  useEffect(() => {
    if (props.order) {
      setLoading(false);
      setData(props.order);
    }
  }, [props.order]);
  const filterData = (date = {}) => {
    let filteredData = [];
    props.order.forEach((item) => {
      if (
        item.year === date.year &&
        item.month === date.month &&
        item.day === date.day
      ) {
        filteredData.push(item);
      }
    });
    setData(filteredData);
  };

  const onSelectedRowKeysChange = (selectedRowKeys) => {
    setSelectedRowKeys(selectedRowKeys);
  };
  const handleSearch = (value) => {
    let searchResults = [];
    props.order.forEach((item) => {
      if (item.email === value || item.orderId === value) {
        searchResults.push(item);
      }
    });
    setData(searchResults);
  };
  const handleReset = () => {
    setData(props.order);
    let inputBox = document.querySelector(".ant-input");
    inputBox.value = "";
  };
  const onDateChange = (date) => {
    date &&
      filterData({
        year: date._d.getFullYear(),
        month: date._d.getMonth() + 1,
        day: date._d.getDate(),
      });
  };
  const handleRefund = async (orderId, index) => {
    setRefundLoading(true);
    setRefundIndex(index);
    $axios
      .post("/refund", { orderId })
      .then((res) => {
        setRefundLoading(false);
        message.success("");
        props.handleFetchOrder();
        setRefundIndex(-1);
      })
      .catch((err) => {
        setRefundLoading(false);
        setRefundIndex(-1);
        console.log(err);
        message.error("");
      });
  };
  const rowSelection = {
    selectedRowKeys,
    onChange: onSelectedRowKeysChange,
  };
  const columns = [
    {
      title: "",
      dataIndex: "orderId",
      key: "orderId",
      width: 100,
      render: (text, record, index) => (
        <Button
          type="primary"
          onClick={() => {
            handleRefund(record.orderId, index);
          }}
          size="small"
          loading={refundIndex === index && refundLoading}
          disabled={record.paymentStatus !== ""}
        >
          
        </Button>
      ),
    },
    {
      title: "",
      dataIndex: "orderId",
      key: "orderId",
      width: 180,
    },

    {
      title: "",
      key: "productName",
      dataIndex: "productName",
      width: 100,
    },
    {
      title: "",
      key: "levelName",
      dataIndex: "levelName",
      width: 100,
    },
    {
      title: "",
      key: "paymentStatus",
      dataIndex: "paymentStatus",
      width: 100,
      render: (paymentStatus) =>
        paymentStatus === "" ? (
          <Badge status="success" text={paymentStatus} />
        ) : paymentStatus === "" ? (
          <Badge status="error" text={paymentStatus} />
        ) : (
          <Badge status="warning" text={paymentStatus} />
        ),
    },
    {
      title: "",
      key: "code",
      dataIndex: "code",
      width: 220,
    },
    {
      title: "",
      key: "activation",
      dataIndex: "activation",
      width: 140,
      render: (activation) =>
        activation === "" ? (
          <Badge status="success" text={activation} />
        ) : (
          <Badge status="warning" text={activation} />
        ),
    },
    {
      title: "",
      dataIndex: "date",
      key: "date",
      width: 100,
    },

    {
      title: "",
      dataIndex: "price",
      key: "price",
      width: 100,
      render: (price) => <span>{price}</span>,
    },
    {
      title: "",
      dataIndex: "payment",
      key: "payment",
      width: 100,
      render: (payment) =>
        payment === "alipay" ? <span></span> : <span></span>,
    },
    {
      title: "",
      dataIndex: "email",
      key: "email",
      width: 200,
    },
  ];
  const date = new Date();
  return (
    <div className="shadow-radius">
      <div
        className="order-page-header"
        style={{
          backgroundColor: "white",
          height: "100px",
          padding: "30px 20px",
        }}
      >
        <div style={{ fontSize: "20px", fontWeight: "500" }}></div>
        <p style={{ lineHeight: "35px", fontSize: "15px", opacity: "0.8" }}>
          
        </p>
      </div>
      <div
        style={{
          backgroundColor: "white",
          height: "80px",
          margin: "20px 20px 0 20px",
        }}
      >
        <div
          style={{
            marginLeft: "20px",
            float: "left",
            lineHeight: "80px",
            fontSize: "18px",
          }}
        >
          
        </div>
        <Search
          placeholder="Email"
          enterButton=""
          style={{
            width: 300,
            margin: "25px 10px 25px 10px",
            float: "left",
            height: "20px",
            fontSize: "25px",
          }}
          onSearch={(value) => {
            handleSearch(value);
          }}
        />
        <DatePicker
          defaultValue={moment(`${date.toLocaleDateString()}`, dateFormat)}
          format={dateFormat}
          onChange={onDateChange}
          style={{
            width: 200,
            margin: "25px 20px",
            float: "left",
          }}
        />
        <Button
          onClick={handleReset}
          style={{
            margin: "25px 10px",
            float: "left",
            color: "#40A9FF !important",
          }}
        >
          
        </Button>
      </div>
      <div
        className="order-page-body"
        style={{ backgroundColor: "white", margin: "0px 20px" }}
      >
        <Table
          columns={columns}
          dataSource={data}
          rowKey={(record) => record}
          rowSelection={rowSelection}
          style={{ userSelect: "text" }}
          scroll={{ x: 800 }}
          loading={loading}
        />
      </div>
    </div>
  );
};
const mapStateToProps = (state) => {
  return {
    order: state.form.order,
  };
};
const actionCreator = { handleFetchOrder };
export default connect(mapStateToProps, actionCreator)(OrderPage);
