//
import React, { useState } from "react";
import { Form, Input, Button, Row, message } from "antd";
import { AlipayCircleOutlined } from "@ant-design/icons";
import "./index.css";
import { connect } from "react-redux";
import { handleFetchForm, handleVerifyDialog } from "@/redux/actions/form";
import $axios from "@/axios/$axios";
import VerifyId from "../../components/verifyId";
export const Alipay = (props) => {
  const [loading, setLoading] = useState(false);
  const formItemLayoutWithOutLabel = {
    wrapperCol: {
      xs: { span: 24, offset: 2 },
      sm: { span: 16, offset: 4 },
    },
  };
  const formItemLayout = {
    labelCol: {
      sm: { span: 3 },
    },
    wrapperCol: {
      sm: { span: 10, offset: 0 },
    },
  };
  const formRef = React.createRef();
  const handleVerify = () => {
    if (props.isVerified) {
      formRef.current.setFieldsValue(props.formData);
    } else {
      props.handleVerifyDialog(true);
    }
  };
  const onFinish = (values) => {
    setLoading(true);
    if (!props.isVerified) {
      props.handleVerifyDialog(true);
      setLoading(false);
      return;
    }
    $axios
      .post(`/alipay/${props.formData._id}`, {
        ...values,
        paymentName: "",
      })
      .then(() => {
        message.success("");
        setLoading(false);
        props.handleFetchForm();
      })
      .catch((error) => {
        setLoading(false);
        message.error(error.response.data.message);
      });
  };
  return (
    <div className="alipay-container" style={{ position: "relative" }}>
      <VerifyId />
      <Row justify="center" style={{ marginTop: "20px" }}>
        <AlipayCircleOutlined className="alipay-icon" />
        <p className="alipay-title"></p>
      </Row>
      <div className="alipay-page-message">
        <p className="alipay-message-title"> </p>
        <p>
          
          <br />
          <br />
          
          <br />
          <br />
          <a
            href="https://51.ruyo.net/12354.html"
            target="_blank"
            rel="noopener noreferrer"
          >
            
          </a>
        </p>
      </div>
      <Form
        {...formItemLayout}
        onFinish={onFinish}
        ref={formRef}
        name="control-ref"
        initialValues={
          props.isVerified && props.formData ? props.formData : null
        }
        style={{ marginTop: "40px" }}
      >
        <Form.Item label="" name="paymentName">
          <Input placeholder="" defaultValue="" />
        </Form.Item>
        <Form.Item
          label="ID"
          name="appId"
          rules={[
            {
              required: true,
              message: "ID",
            },
          ]}
        >
          <Input placeholder="ID" />
        </Form.Item>
        <Form.Item
          name="publicKey"
          label=""
          rules={[
            {
              required: true,
              message: "",
            },
          ]}
        >
          <Input.TextArea
            placeholder=""
            autoSize={{ minRows: 3, maxRows: 5 }}
          />
        </Form.Item>
        <Form.Item
          name="secretKey"
          label=""
          rules={[
            {
              required: true,
              message: "",
            },
          ]}
        >
          <Input.TextArea
            placeholder="RSA2(SHA256)"
            autoSize={{ minRows: 3, maxRows: 5 }}
          />
        </Form.Item>
        <Form.Item
          label=""
          name="notifyUrl"
          rules={[
            {
              required: true,
              message: "",
            },
          ]}
        >
          <Input placeholder="httphttps" />
        </Form.Item>
        <Form.Item {...formItemLayoutWithOutLabel}>
          <Button onClick={handleVerify} style={{ marginRight: "10px" }}>
            
          </Button>
          <Button type="primary" htmlType="submit" loading={loading}>
            
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
};

const mapStateToProps = (state) => {
  return { isVerified: state.form.isVerified };
};
const actionCreator = {
  handleFetchForm,
  handleVerifyDialog,
};
export default connect(mapStateToProps, actionCreator)(Alipay);
