import React, { useState } from "react";
import { Form, Input, Button, Row, Select, message } from "antd";
import { WechatOutlined } from "@ant-design/icons";
import "./index.css";
import $axios from "@/axios/$axios";
const { Option } = Select;
const WechatPay = (props) => {
  const [loading, setLoading] = useState(false);

  const onFinish = (values) => {
    setLoading(true);

    $axios
      .post(`/wechatPay/${props.formData._id}`, values)
      .then(() => {
        message.success("");
        setLoading(false);
      })
      .catch(() => {
        message.error("");
        setLoading(false);
      });
  };
  const formItemLayoutWithOutLabel = {
    wrapperCol: {
      xs: { span: 24, offset: 2 },
      sm: { span: 16, offset: 7 },
    },
  };
  const formItemLayout = {
    labelCol: {
      sm: { span: 3 },
    },
    wrapperCol: {
      sm: { span: 10, offset: 0 },
    },
  };
  return (
    <div className="wechat-container" style={{ position: "relative" }}>
      <Row justify="center" style={{ marginTop: "20px" }}>
        <WechatOutlined className="wechat-icon" />
        <p className="wechat-title"></p>
      </Row>
      <div
        className="alipay-page-message"
        style={{ borderLeft: "4px solid #00bb0d" }}
      >
        <p className="alipay-message-title"> </p>
        <p>
          
          <br />
          <br />
          Github star 
          <br />
          <br />
          <a href="https://" target="_blank" rel="noopener noreferrer">
            
          </a>
        </p>
      </div>
      <Form
        {...formItemLayout}
        onFinish={onFinish}
        // initialValues={props.formData ? props.formData : null}
        style={{ marginTop: "40px" }}
      >
        <Form.Item label="" name="paymentName">
          <Input placeholder="" defaultValue="" />
        </Form.Item>
        <Form.Item
          label="ID"
          name="accountID"
          rules={[
            {
              required: true,
              message: "ID",
            },
          ]}
        >
          <Input placeholder="ID" />
        </Form.Item>
        <Form.Item
          label="ID"
          name="bussinessId"
          rules={[
            {
              required: true,
              message: "ID",
            },
          ]}
        >
          <Input.TextArea
            placeholder="ID"
            autoSize={{ minRows: 3, maxRows: 5 }}
          />
        </Form.Item>
        <Form.Item
          name="secretKey"
          label=""
          rules={[
            {
              required: true,
              message: "",
            },
          ]}
        >
          <Input.TextArea
            placeholder=""
            autoSize={{ minRows: 3, maxRows: 5 }}
          />
        </Form.Item>
        <Form.Item
          name="signMethod"
          label=""
          rules={[
            {
              required: true,
              message: "",
            },
          ]}
        >
          <Select placeholder="">
            <Option value="MD5">MD5</Option>
            <Option value="SHA256">SHA256</Option>
          </Select>
        </Form.Item>
        <Form.Item
          label=""
          name="answer1"
          rules={[
            {
              required: true,
              message: "",
            },
          ]}
        >
          <Input placeholder="" />
        </Form.Item>
        <Form.Item
          label=""
          name="answer2"
          rules={[
            {
              required: true,
              message: "",
            },
          ]}
        >
          <Input placeholder="" />
        </Form.Item>
        <Form.Item {...formItemLayoutWithOutLabel}>
          <Button type="primary" htmlType="submit" disabled loading={loading}>
            
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
};

export default WechatPay;
