import moment from "moment";
export const monthAxis = () => {
  let axis = [];
  for (let i = 1; i <= 12; i++) {
    axis.push(`${i}`);
  }
  return axis;
};
export const dayAxis = () => {
  let maxDay = moment().daysInMonth();
  let axis = [];
  for (let i = 1; i <= maxDay; i++) {
    axis.push(`${i}`);
  }
  return axis;
};
export const weekAxis = () => {
  let axis = [];
  for (let i = 1; i <= 7; i++) {
    axis.push(`${romanToChinese(i)}`);
  }
  return axis;
};
export const romanToChinese = (num) => {
  let Chinese = "";
  switch (num) {
    case 1:
      Chinese = "";
      break;
    case 2:
      Chinese = "";
      break;
    case 3:
      Chinese = "";
      break;
    case 4:
      Chinese = "";
      break;
    case 5:
      Chinese = "";
      break;
    case 6:
      Chinese = "";
      break;
    case 7:
      Chinese = "";
      break;
    default:
      break;
  }
  return Chinese;
};
const xAxisOption = (range) => {
  return [
    {
      type: "category",
      show: true,
      data:
        range === "year"
          ? monthAxis()
          : range === "month"
          ? dayAxis()
          : weekAxis(),
    },
  ];
};
const seriesOption = (catergory, range, data) => {
  return [
    {
      name:
        catergory === "sales"
          ? ""
          : catergory === "visits"
          ? ""
          : "",
      type: "bar",
      data: data,
      itemStyle: {
        normal: {
          // 
          color: "#58AFFF",
          // 
          label: {
            show: false,
            position: "top",
            formatter: "{c}",
            // formatter: '{b}\n{c}'
          },
        },
      },
      barWidth:
        range === "week"
          ? 40
          : range === "month"
          ? 15
          : range === "year"
          ? 30
          : null,
    },
    // ~
  ];
};
const barChartData = {
  backgroundColor: "#fff",
  // title: {
  //   top: 30,
  //   text: "",
  //   textStyle: {
  //     fontWeight: "normal",
  //     fontSize: 16,
  //     color: "#57617B"
  //   },
  //   left: "center"
  // },
  tooltip: {
    trigger: "axis",
  },
  grid: {
    top: "2%",
    left: "0%",
    right: "0.5%",
    bottom: "0%",
    containLabel: true,
    show: false,
  },
  // x
  yAxis: [
    {
      type: "value",
      show: true,
      axisLabel: {
        formatter: "{value}",
      },
    },
  ],
};
export function chartData(catergory, range, data) {
  return {
    ...barChartData,
    xAxis: xAxisOption(range),
    series: seriesOption(catergory, range, data),
  };
}
