import React, { useState } from "react";
import { Menu, Form, Input, Button, Descriptions, message, Modal } from "antd";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import $axios from "@/axios/$axios";
const { Item } = Menu;
const formItemLayoutWithOutLabel = {
  wrapperCol: {
    xs: { span: 24, offset: 12 },
    sm: { span: 16, offset: 8 },
  },
};
const formItemLayout = {
  labelCol: {
    sm: { span: 8 },
  },
  wrapperCol: {
    sm: { span: 12, offset: 0 },
  },
};
const menuMap = {
  info: "",
  changeInfo: "",
  // notification: "New Message Notification"
};
const AccountPage = (props) => {
  const [loading, setLoading] = useState(false);
  const [selectedKey, setSelectedKey] = useState("info");
  const info = () => {
    Modal.info({
      title: "",
      content: (
        <div>
          <p> Github </p>
        </div>
      ),
      onOk: () => {
        setLoading(false);
      },
    });
  };

  const onFinish = (values) => {
    setLoading(true);
    $axios
      .post(`/user/update/${props.user._id}`, values)
      .then(() => {
        message.success("");
        setLoading(false);
      })
      .catch(() => {
        message.error("");
        setLoading(false);
      });
  };
  const checkUpdate = async () => {
    setLoading(true);

    await $axios
      .get("/setting")
      .then((result) => {
        if (result.data.version > props.setting.version) {
          info();
        } else {
          message.success("");
          setLoading(false);
        }
      })
      .catch(() => {
        message.error("");
        setLoading(false);
      });
  };
  const getMenu = () => {
    return Object.keys(menuMap).map((item) => (
      <Item key={item}>{menuMap[item]}</Item>
    ));
  };
  const selectKey = (key) => {
    setSelectedKey(key);
  };
  const renderAccountInfo = () => {
    return (
      <Descriptions title="">
        <Descriptions.Item label="">{props.user.email}</Descriptions.Item>
        <Descriptions.Item label="">
          {props.user.date}
        </Descriptions.Item>
        <Descriptions.Item label="">
          {props.setting.version}
        </Descriptions.Item>
        <Descriptions.Item label="">
          <Button type="primary" onClick={checkUpdate} loading={loading}>
            
          </Button>
          <a
            href="https://github.com/troyeguo/coodo-pay"
            target="_blank"
            rel="noopener noreferrer"
          >
            &nbsp;&nbsp;&nbsp;
          </a>
        </Descriptions.Item>
      </Descriptions>
    );
  };
  const renderChangeInfo = () => {
    return (
      <Form
        {...formItemLayout}
        onFinish={onFinish}
        initialValues={props.formData ? props.formData : null}
        style={{ marginTop: "40px" }}
      >
        <Form.Item
          label=""
          name="email"
          rules={[
            {
              required: true,
              message: "",
            },
          ]}
        >
          <Input placeholder="" />
        </Form.Item>
        <Form.Item
          label=""
          name="password"
          rules={[
            {
              required: true,
              message: "",
            },
          ]}
        >
          <Input.Password placeholder="" />
        </Form.Item>

        <Form.Item
          label=""
          name="answer1"
          rules={[
            {
              required: true,
              message: "",
            },
          ]}
        >
          <Input placeholder="" />
        </Form.Item>
        <Form.Item
          label=""
          name="answer2"
          rules={[
            {
              required: true,
              message: "",
            },
          ]}
        >
          <Input placeholder="" />
        </Form.Item>
        <Form.Item {...formItemLayoutWithOutLabel}>
          <Button type="primary" htmlType="submit" loading={loading}>
            
          </Button>
        </Form.Item>
      </Form>
    );
  };

  const renderChildren = () => {
    switch (selectedKey) {
      case "info":
        return renderAccountInfo();

      case "changeInfo":
        return renderChangeInfo();
      default:
        break;
    }

    return null;
  };
  return (
    <div className="main" style={{ height: "100%" }}>
      <div className={"leftMenu"}>
        <Menu
          mode="inline"
          selectedKeys={[selectedKey]}
          onClick={({ key }) => selectKey(key)}
          className="payment-page"
          style={{
            marginTop: "10px",
          }}
        >
          {getMenu()}
        </Menu>
      </div>
      <div className={"right"}>{renderChildren()}</div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    user: state.form.user,
    setting: state.product.setting,
  };
};
const actionCreator = {};
export default connect(mapStateToProps, actionCreator)(withRouter(AccountPage));
